*, *::before, *::after {
  box-sizing: border-box;
}

html {
  height: 100%;
  font-size: 100%;
}

body {
  color: var(--color-text-black);
  margin: 0;
  font-family: "Raleway", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

#root {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

h1, h2, h3, p {
  margin: 0;
}

h1 {
  font-size: 32px;
  font-weight: 500;
}

h2 {
  font-size: 24px;
  font-weight: 400;
}

a {
  text-decoration: none;
  color: var(--color-text-black);
}

button {
  font-family: "Raleway";
}

/* –≠—Ç–æ—Ç —Å–µ–ª–µ–∫—Ç –≤—ã–ø–µ–ª–µ–Ω, –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –ø–æ–ª–Ω–æ—Å—Ç—å—é –∫–∞—Å—Ç–æ–º–Ω—ã–π
select {
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  font-weight: 400;

  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  border: none;
  cursor: pointer;
  background-color: transparent;
  outline: none;
}

select::-ms-expand {
  display: none;
} */

input {
  font-family: "Raleway", sans-serif;
  font-size: 14px;
}

/* —É–±–∏—Ä–∞–µ–º —Å—Ç—Ä–µ–ª–∫–∏ —É input —Å —Ç–∏–ø–æ–º number */
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}


/* –ê–Ω–∏–º–∞—Ü–∏—è –¥–ª—è –∫–∞—Å—Ç–æ–º–Ω–æ–≥–æ —Å–ª–∞–π–¥–µ—Ä–∞ */
.slider {
  width: 100%;
  height: 1.5px;
  background-color: var(--color-text-black);
  margin-top: 10px;
}

.slider .thumb {
  width: 17px;
  height: 17px;
  background-color: var(--color-yellow);
  cursor: pointer;
  border-radius: 3px;
  top: -8.5px;
}

.slider .thumb:focus-visible {
  outline: none;
}

/* –î–æ–±–∞–≤–ª—è–µ–º —Å–≤–æ–∏ —à—Ä–∏—Ñ—Ç—ã */
@font-face {
  font-family: "Gothampro";
  src: url("./fonts/gothampro_black.ttf");
}

@font-face {
  font-family: "Raleway";
  src: url("./fonts/Raleway-VariableFont_wght.ttf");
  font-weight: 400;
}