.button {
    border: none;
    padding: 10px;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    cursor: pointer;
    position: relative;
    transition: all 0.3s;
    
    color: var(--color-text-black);
    font-size: 16px;
    /* line-height: 18.78; —É–≤–µ–ª–∏—á–∏–≤–∞–µ—Ç –≤—ã—Å–æ—Ç—É —Ç–µ–∫—Å—Ç–∞ –≤—ã—à–µ, —á–µ–º –≤–µ—Å–æ—Ç–∞ –∫–Ω–æ–ø–∫–∏ */
}

.buttonYellow {
    background-color: var(--color-yellow);
}

.buttonYellow:hover {
    background-color: var(--color-blue);
    color: white;
}

.buttonYellow:active {
    top: 1px;
    background-color: var(--color-blue-dark);
}

/* –ï—Å–ª–∏ –≤ –∫–Ω–æ–ø–∫–µ –±—É–¥—É—Ç –∏–∫–æ–Ω–∫–∏, —Ç–æ –ø—Ä–∏ —Ö–æ–≤–µ—Ä–µ –æ–Ω–∏ –ø–æ–¥—Å–≤–µ—Ç—è—Ç—Å—è –±–µ–ª—ã–º */
.buttonYellow:hover * {
    stroke: white;
}